<template>
  <div>
    <div class="header">
      <div @click="goBack">
        <van-icon name="arrow-left" size="22" />
      </div>
      <div>{{$t('login.backup')}}</div>
      <div></div>
    </div>
    <main>
      <div class="content">
        <div class="tips">{{$t('login.book')}}</div>
        <div class="inputBorder">
          <div v-for="(item,index) in sort" @click="noSort(item)" :key="index">{{item}}</div>
        </div>
        <div class="items">
          <div v-for="(item,index) in zhuji" :key="index" @click="sorts(item)">{{item}}</div>
        </div>

        <div class="btn" @click="sure">{{$t('login.cpmplete')}}</div>
      </div>
    </main>
  </div>
</template>

<script>
export default {
  data() {
    return {
      zhuji: [],
      sort: [],
      id: '',
      uuid: '',
    }
  },
  methods: {
    // 获取打乱后的助记词
    getTerms(id) {
      this.$toast.loading({
        duration: 60000,
        forbidClick: true,
        message: this.$t('other.loading'),
        loadingType: 'spinner',
      })
      this.$post('/login/zhuji_luan', { id: id }).then((res) => {
        console.log(res)
        this.$toast.clear()
        if (res.code == '1') {
          this.zhuji = res.data.zhujici
        }
      })
    },
    goBack() {
      this.$router.push('/createAddress')
    },
    // 排序
    sorts(item) {
      this.sort.push(item)
      let index = this.zhuji.findIndex((i) => {
        return i == item
      })
      this.zhuji.splice(index, 1)
    },
    noSort(item) {
      this.zhuji.push(item)
      let index = this.sort.findIndex((i) => {
        return i == item
      })
      this.sort.splice(index, 1)
    },
    // 确定
    async sure() {
      if (this.zhuji.length != 0) {
        this.$toast(this.$t('other.wancheng'))
      } else {
        // this.getDeviceInfo()
        this.$toast.loading({
          duration: 60000,
          forbidClick: true,
          message: this.$t('other.loading'),
          loadingType: 'spinner',
        })
        const { data: res } = await this.$http.post('/login/zhuji_duibi', {
          id: this.id,
          zhuji: JSON.stringify(this.sort),
        })
        if (res.code == '1') {
          this.$toast.clear()
          window.location.href = res.data.app_url
        } else {
          this.$toast(res.msg)
        }
      }
    },
    // 登录
  },
  created() {
    this.getTerms(localStorage.getItem('id'))
    this.id = localStorage.getItem('id')
    this.uuid = localStorage.getItem('onlyId')
  },
  destroyed() {
    localStorage.removeItem('id')
  },
}
</script>

<style lang="less" scoped>
.header {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding: 0 0.333333rem;
  height: 1.226667rem;
  background-color: #212126;
  div {
    height: 100%;
    display: flex;
    align-items: center;
  }
  div:nth-child(1) {
    width: 20%;
  }
  div:nth-child(2) {
    width: 60%;
    justify-content: center;
    font-size: 0.48rem;
  }
  div:nth-child(3) {
    width: 20%;
    justify-content: flex-end;
  }
}
main {
  height: calc(100vh - 1.226667rem);
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}
.content {
  box-sizing: border-box;
  padding: 0 0.333333rem;
}
.tips {
  text-align: center;
  font-size: 0.48rem;
  margin: 1.946667rem 0;
}
.inputBorder {
  border: 0.026667rem solid #2c2c2c;
  border-radius: 0.133333rem;
  min-height: 4.56rem;
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  box-sizing: border-box;
  div {
    padding: 0.106667rem 0.2rem;
    background-color: #fff;
    border-radius: 0.066667rem;
    font-size: 0.32rem;
    color: #676767;
    margin-left: 0.206667rem;
    margin-top: 0.506667rem;
  }
}
.items {
  width: 100%;
  margin: 0.5rem auto 0;
  display: flex;
  flex-wrap: wrap;
  div {
    padding: 0.106667rem 0.2rem;
    background-color: #fff;
    border-radius: 0.066667rem;
    font-size: 0.32rem;
    color: #676767;
    margin-left: 0.206667rem;
    margin-top: 0.506667rem;
  }
}
.btn {
  width: 90%;
  text-align: center;
  height: 1.2rem;
  line-height: 1.2rem;
  color: #000000;
  font-size: 0.426667rem;
  border-radius: 0.6rem;
  background: linear-gradient(to top, #eec780, #f6dca7);
  background: -webkit-linear-gradient(to top, #eec780, #f6dca7);
  margin: 1.66rem auto 0.46rem;
}
</style>
